"use client"

import { DashboardLayout } from "@/components/dashboard-layout"
import { useAuth } from "@/components/auth-provider"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Users, Calendar, FileCheck, BookOpen, Target, TrendingUp } from "lucide-react"

function StatsCard({ title, value, description, icon: Icon }: { 
  title: string
  value: string | number
  description: string
  icon: React.ElementType 
}) {
  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
        <CardTitle className="text-sm font-medium">{title}</CardTitle>
        <Icon className="h-4 w-4 text-muted-foreground" />
      </CardHeader>
      <CardContent>
        <div className="text-2xl font-bold">{value}</div>
        <p className="text-xs text-muted-foreground">{description}</p>
      </CardContent>
    </Card>
  )
}

export default function DashboardPage() {
  const { user } = useAuth()

  const getUserName = () => {
    if (!user) return ''
    if (user.role === 'STUDENT' && user.student?.name) return user.student.name
    if ((user.role === 'FACULTY' || user.role === 'HOD') && user.faculty?.name) return user.faculty.name
    return user.email
  }

  const renderDashboardContent = () => {
    switch (user?.role) {
      case 'ADMIN':
        return (
          <>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
              <StatsCard title="Total Users" value={0} icon={Users} description="Registered in system" />
              <StatsCard title="Faculty" value={0} icon={Users} description="Active faculty members" />
              <StatsCard title="Students" value={0} icon={Users} description="Registered students" />
              <StatsCard title="Pending Requests" value={0} icon={FileCheck} description="Awaiting review" />
            </div>
          </>
        )
      case 'HOD':
        return (
          <>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
              <StatsCard title="Department Faculty" value={0} icon={Users} description="In your department" />
              <StatsCard title="Department Students" value={0} icon={Users} description="In your department" />
              <StatsCard title="Pending Approvals" value={0} icon={FileCheck} description="Awaiting your review" />
              <StatsCard title="Unassigned Students" value={0} icon={Users} description="Need mentor assignment" />
            </div>
          </>
        )
      case 'FACULTY':
        return (
          <>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              <StatsCard title="Assigned Students" value={0} icon={Users} description="Mentees under your guidance" />
              <StatsCard title="Upcoming Meetings" value={0} icon={Calendar} description="Scheduled this week" />
              <StatsCard title="Pending Requests" value={0} icon={FileCheck} description="Student requests" />
            </div>
          </>
        )
      case 'STUDENT':
        return (
          <>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              <StatsCard title="Current GPA" value="-" icon={BookOpen} description="Latest semester" />
              <StatsCard title="Goals Progress" value="-" icon={Target} description="Career milestones" />
              <StatsCard title="Next Meeting" value="-" icon={Calendar} description="With your mentor" />
            </div>
          </>
        )
      default:
        return null
    }
  }

  return (
    <DashboardLayout>
      <div className="space-y-6">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">
            {user?.role === 'ADMIN' && 'Admin Dashboard'}
            {user?.role === 'HOD' && 'HOD Dashboard'}
            {user?.role === 'FACULTY' && 'Faculty Dashboard'}
            {user?.role === 'STUDENT' && 'Student Dashboard'}
          </h1>
          <p className="text-muted-foreground mt-1">
            Welcome back, {getUserName()}
          </p>
        </div>

        {renderDashboardContent()}

        {/* Quick Actions */}
        <Card>
          <CardHeader>
            <CardTitle>Quick Actions</CardTitle>
            <CardDescription>Common tasks for your role</CardDescription>
          </CardHeader>
          <CardContent>
            <p className="text-sm text-muted-foreground">
              Use the sidebar to navigate to different sections based on your role.
            </p>
          </CardContent>
        </Card>
      </div>
    </DashboardLayout>
  )
}
